"use client";

import React, { useState } from "react";
import { Button } from "@/components/ui/button";
import { Label } from "@/components/ui/label";
import { Slider } from "@/components/ui/slider";
import useURL from "@/hooks/useURL";
import SingleSelectFilters from "./SingleSelectFilters";
import MultipleSelectFilters from "./MultipleSelectFilters";
import { useFilterProvider } from "../provider/FilterProvider";
type FilterSelectProps = {
  onClose: () => void;
};
export function FilterSelect({
  onClose
}: FilterSelectProps) {
  const {
    update,
    getValue
  } = useURL();
  const {
    writer,
    corner,
    shareHouse,
    category,
    subcategory
  } = useFilterProvider();
  const [priceRange, setPriceRange] = useState<[number, number]>(getValue("priceRange")?.split("%") as unknown as [number, number] ?? [0, 2000]);
  const handleApply = () => {
    update([{
      name: "writer",
      value: writer.id ? writer.id + "%" + writer.name : ""
    }, {
      name: "shareHouse",
      value: shareHouse.id ?? ""
    }, {
      name: "corner",
      value: corner.id ?? ""
    }, {
      name: "categories",
      value: category.map(c => c.id).join("%") as string
    }, {
      name: "subcategories",
      value: subcategory.map(s => s.id).join("%") as string
    }, {
      name: "priceRange",
      value: priceRange.join("%") as string
    }]);
    onClose();
  };
  return <div className="space-y-4 p-4" data-sentry-component="FilterSelect" data-sentry-source-file="FilterSelect.tsx">
      <div className="space-y-2">
        <Label data-sentry-element="Label" data-sentry-source-file="FilterSelect.tsx">السعر</Label>
        <Slider min={0} max={2000} step={1} value={priceRange} onValueChange={(value: [number, number]) => setPriceRange(value)} className="[&_.range-slider__range]:bg-color1 [&_[role=slider]]:border-black [&_[role=slider]]:bg-color2 [&_[role=slider]]:focus:ring-color1" color="#27a098" data-sentry-element="Slider" data-sentry-source-file="FilterSelect.tsx" />
        <div className="flex justify-between text-sm">
          <span>${priceRange[0]}</span>
          <span>${priceRange[1]}</span>
        </div>
      </div>

      <SingleSelectFilters data-sentry-element="SingleSelectFilters" data-sentry-source-file="FilterSelect.tsx" />
      <MultipleSelectFilters data-sentry-element="MultipleSelectFilters" data-sentry-source-file="FilterSelect.tsx" />

      <Button onClick={handleApply} className="w-full bg-color1 hover:bg-color2" data-sentry-element="Button" data-sentry-source-file="FilterSelect.tsx">
        تطبيق المرشحات
      </Button>
    </div>;
}